body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}
#root {
    /* flex-контейнер для <header>, <main> и <footer> */
    display: flex;
    /* flex-элементы <header>, <main> и <footer> выстаиваются по вертикали */
    flex-direction: column;
    /* вся высота viewport браузера */
    min-height: 100vh;
}
main {
    padding-top: 20px;
    /*
     * может как увеличиваться, так и уменьшаться, чтобы вместе с
     * <header> и <footer> занять всю высоту viewport браузера
     */
    flex: 1 1 auto;
}
.page-footer {
    padding-top: 10px;
    padding-bottom: 10px;
}
nav {
    box-shadow: none;
}

.items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.card {
    display: flex;
    flex-direction: column;
}

.card .card-content {
    flex-grow: 1;
}

.card .card-title {
    font-size: 18px;
}
.btn:hover, .btn-large:hover, .btn-small:hover {
    background-color: #ffe900;
    color:#e46cb1
  }
.btn, .btn-large, .btn-small, .btn-flat {
    padding: 0 10px;
    text-decoration: none;
    color: #ffffff;
    background-color: #e46cb1;
    text-align: center;
    letter-spacing: .5px;
    -webkit-transition: background-color .2s ease-out;
    transition: background-color .2s ease-out;
    cursor: pointer;
}

.cart-icon {
    position: fixed;
    z-index: 100;
    right: 10px;
    top: 10px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #e46cb1;
    background-color: #ffe900;
}

.cart-modal {
    position: fixed;
    z-index: 50;
    min-width: 50%;
    max-width: 100%;
    max-height: 80vh;
    overflow-y: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 10px;
    border: 3px solid #e46cb1;
}
.red-text.text-lighten-1 {
    color: #e46cb1 !important;
}
.cart-modal-close {
    position: absolute;
    top: 0;
    right: 0;
    color: #e46cb1;
    cursor: pointer;
    font-weight: bold;
}

.cart-item-delete {
    cursor: pointer;
    color: #e46cb1;
}

.show-alert {
    position: fixed;
    z-index: 100;
    right: 60px;
    top: 10px;
    background-color: #000;
    color: #fff;
    padding: 10px;
}
.teal.lighten-1 {
        background-color: #e46cb1 !important;
}
.progress .indeterminate {
    background-color: #e46cb1;
}
